<template>
    <span v-if="isSingle">{{ singular }}</span>
    <span v-else>{{ plural }}</span>
</template>

<script>
    export default {
        name: "Plural",
        props: [
            "number",
            "plural",
            "singular"
        ],
        computed: {
            isSingle: function () {
                return this.number === 1;
            }
        }
    }
</script>