<template>
    <div>
        <h2>Тарифы</h2>
        <ul>
            <li>
                <label>
                    <input type="radio" name="type" value="1.5" v-model="type">
                    <img src="../assets/icons/one_and_half.png">
                    <span>Полуторатонник</span>
                </label>
            </li>
            <li>
                <label>
                    <input type="radio" name="type" value="3" v-model="type">
                    <img src="../assets/icons/three.png">
                    <span>Трехтонник</span>
                </label>
            </li>
            <li>
                <label>
                    <input type="radio" name="type" value="5" v-model="type">
                    <img src="../assets/icons/five.png">
                    <span>Пятитонник</span>
                </label>
            </li>
            <li>
                <label>
                    <input type="radio" name="type" value="trash" v-model="type">
                    <img src="../assets/icons/trash.png">
                    <span>Вывоз мусора</span>
                </label>
            </li>
            <li>
                <label>
                    <input type="radio" name="type" value="path" v-model="type">
                    <img src="../assets/icons/path.png">
                    <span>Экспедитор</span>
                </label>
            </li>
            <li>
                <label>
                    <input type="radio" name="type" value="none" v-model="type">
                    <img src="../assets/icons/loaders.png">
                    <span>Только грузчики</span>
                </label>
            </li>
        </ul>
        <div v-show="path">
            <label>
                <span>Точек маршрута</span>
                <input type="number" min="1" max="50" v-model="points"/>
            </label>
        </div>
        <h2>Грузчики</h2>
        <div>
            <label>
                <input type="checkbox" v-model="loaders">
                <span></span>
                <span>Нужны</span>
            </label>
            <label v-show="loaders">
                <select v-model="loaders_type">
                    <option value="relocation">Переезд</option>
                    <option value="building_materials">Стройматериалы</option>
                    <option value="volumetric_or_dangerous">Объемные / опасные грузы</option>
                </select>
                <span>
                    Минимум {{ this.loaders_min_time }}
                    <Plural :number="this.loaders_min_time" :singular="'час'" :plural="'часа'"></Plural>
                </span>
            </label>
        </div>
        <div>
            <label v-show="loaders">
                <span>Часы аренды</span>
                <input type="number" :min="loaders_min_time" max="24" v-model="loaders_time">
            </label>
        </div>
        <h2 v-show="final_price">Смета</h2>
        <table v-show="final_price">
            <tr v-show="type && type !== 'none' && type !== 'path'">
                <td>{{ safe_type.label }}</td>
                <td>от {{ safe_type.price }}  &#8381;</td>
            </tr>
            <tr v-show="type && type === 'path'">
                <td>{{ safe_type.label }}</td>
                <td>{{ safe_type.price }}  &#8381; / точка маршрута</td>
            </tr>
            <tr v-show="loaders">
                <td>Грузчики</td>
                <td>{{ loaders_price[loaders_type].price }} &#8381; / час</td>
            </tr>
            <tr>
                <td>Итого:</td>
                <td>{{ final_price }} &#8381;</td>
            </tr>
        </table>
        <small v-show="final_price">
            Сумма не является окончательной, для подсчета стоимости вашего заказа свяжитесь с диспетчером.
        </small>
    </div>
</template>

<script>
    import Plural from "./Plural";
    export default {
        name: "calculator",
        components: {Plural},
        data: function () {
            return {
                type: "",
                points: 1,
                path: false,
                loaders: false,
                type_price: {
                    "1.5": {
                        price: 410,
                        label: "Полуторатонник"
                    },
                    "3": {
                        price: 700,
                        label: "Трехтонник"
                    },
                    "5": {
                        price: 850,
                        label: "Пятитонник"
                    },
                    "trash": {
                        price: 1800,
                        label: "Вывоз мусора"
                    },
                    "path": {
                        price: 150,
                        label: "Экспедитор"
                    },
                    "none": {
                        price: 0,
                        label: "Только грузчики"
                    },
                },
                loaders_time: 0,
                loaders_type: "relocation",
                loaders_price: {
                    "relocation": {
                        price: 250,
                        min_time: 3,
                        label: "Переезд"
                    },
                    "building_materials": {
                        price: 300,
                        min_time: 3,
                        label: "Стройматериалы",
                    },
                    "volumetric_or_dangerous": {
                        price: 330,
                        min_time: 3,
                        label: "Объемные / опасные грузы",
                    },
                }
            }
        },
        computed: {
            loaders_min_time: function () {
                return this.loaders_price[this.loaders_type].min_time
            },
            safe_type: function () {
                if (this.type_price[this.type]) {
                    return this.type_price[this.type]
                } else return {};
            },
            final_price: function () {
                let price = 0;
                if (this.type) {
                    price += this.type_price[this.type].price;
                    if (this.type === "path") price *= this.points;
                }
                if (this.loaders) {
                    price += this.loaders_price[this.loaders_type].price * this.loaders_time;
                }
                return price;
            }
        },
        watch: {
            type: function (value) {
                this.path = value === "path";
                this.loaders = value === "none";

            },
            loaders: function (value) {
                if (this.type === "" && value === true) this.type = "none";
                if (this.type === "none" && value === false) this.type = "";
            },
            points: function () {
                this.points = Math.max(1, Math.min(this.points, 50))
            },
            loaders_time: function () {
                this.loaders_time = Math.max(this.loaders_min_time, Math.min(this.loaders_time, 24))
            },
            loaders_type: function () {
                this.loaders_time = this.loaders_min_time;
            }
        },
        created: function () {
            this.loaders_time = this.loaders_min_time;
        }
    }
</script>

